span {
  display: inline-block;
  height: 15px;
  width: 15px;
  background: #2FAC9B;
  border-radius: 0px;
}
#preloader-element {
  margin: 200px auto;
  text-align: center;
}
.preloader {
  width: 100%;
  height: 100%;
  background-color: #FFF;
  position: absolute;
  opacity: 0.8;
  z-index: 2;
  margin: 0 auto;
}
#preloader-element span:nth-child(1) {
  animation: temp 1s 0.05s infinite cubic-bezier(0.005, 0.56, 0.58, 1.59);
  width: 7.5px;
  height: 7.5px;
  margin: 0 2px;
}
